.flickity-enabled{
    --flickityArrowTransitionProperty: background-color, color, border-radius;
}

.flickity-button{
    transform: translateY(-50%);
    padding: 0;
    width: 42px;
    height: 58px;
    border-radius: var(--flickityArrowBorderRadius, 2px);
    background: transparent;
    background-color: var(--flickityArrowBgColor, transparent) !important;
    border-width: 1px;
    border-style: solid;
    border-color: var(--flickityArrowBorderColor, var(--colorP1));
    opacity: 1 !important;
    transition-property: var(--flickityArrowTransitionProperty);
    transition-timing-function: ease-in-out;
    transition-duration: 0.2s;
    cursor: pointer;
    &::after{
        content: '\e800';
        font-family: 'fa-fontello';
        position: absolute;
        top: 50%;
        left: 50%;
        font-size: 20px;
        transition: color ease-in-out 0.2s;
        color: var(--flickityArrowIconColor, var(--colorE5));
        transform: translateX(-50%) translateY(-50%) rotate(-90deg);
    }
    &.previous{
        transform: scaleX(-1) translateY(-50%);
    }
    &:hover{
        --flickityArrowBorderRadius: 22px;
        background-color: var(--flickityArrowBgColorHover, rgba(211, 220, 230, 0.26)) !important;
        border-color: var(--flickityArrowBorderColorHover, var(--colorE5));
    }
    &:active{
        background-color: var(--flickityArrowBgColorActive, var(--colorE5)) !important;
        &::after{
            color: var(--flickityArrowIconColorActive, var(--colorP4));
        }
    }
    .flickity-button-icon{
        display: none;
    }
}

.flickity-page-dots{
	--orderedListGap: 0;
	--orderedListStyle: none;
    .dot{
        width: 10px;
        height: 10px;
        margin: 0 6px;
        background: transparent;
        background-color: var(--flickityDotColor, var(--colorS1));
        transition: background-color ease-in-out 0.2s;
        opacity: 1;
        &:hover{
            background-color: var(--flickityDotColorHover, var(--colorP1));
        }
        &.is-selected{
            background-color: var(--flickityDotColorActive, var(--colorE5));
        }
    }
}

.alternate_color{
    .flickity-enabled{
        --flickityArrowBorderColor: var(--colorP4);
        --flickityArrowBorderColorHover: var(--colorP2);
        --flickityArrowBgColorActive: var(--colorP2);
        --flickityArrowIconColor: var(--colorP4);
        --flickityDotColor: var(--colorP4);
        --flickityDotColorHover: rgba(255, 255, 255, 0.4);
        --flickityDotColorActive: var(--colorP2);
    }
}

.flickity-viewport{
    transition: height ease 0.2s;
}
.flickity-enabled.is-fade{
    .ep-flickity-slide{
        opacity: 0 !important;
        transition: opacity ease-in-out 0.2s;
        &.is-selected{
            opacity: 1 !important;
        }
    }
}

